@import "../../variables.module.scss";

.container {
  background-color: $color-secondary;
  min-height: 100vh;

  .innerContainer {
    max-width: $appWidth;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
      font-family: $font-primary;
      font-size: 40px;
      font-weight: 300;
      color: $color-primary;
      text-align: center;
      padding: 20px 0 0 0;
      margin-top: 0;
    }

    h3 {
      color: $color-primary;
      font-family: $font-primary;
      font-weight: 400;
      font-size: 24px;
      text-align: center;
    }

    .story {
      font-family: $font-secondary;
      font-size: 18px;
      line-height: 30px;
      color: $color-primary;
      margin: 0 10px;
      max-width: 860px;
    }
  }
}
