@import "../../variables.module.scss";

@mixin centered {
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  @include centered;
  margin: 0 auto;

  .circle {
    @include centered;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: solid 2px $color-quaternary;
    .arrow {
      color: $color-quaternary;
      transform: rotate(0deg);
    }
  }
}
